import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Container from '@material-ui/core/Container';
import Morphemes from './data/Morphemes.js';

class SearchBar extends Component {

    constructor() {
        super()
        this.state = {      
            search: ''
        } 
    }

    searchSpace = (event) => {
        let keyword = event.target.value;
        this.setState({ search: keyword })
    }

    render() {
        const items = Object.values(Morphemes).map((Name) => (
            Object.values(Name).map(Data => Data))).flat().filter((data) => {
            if (this.state.search === '')
                return null
            else if (data.morpheme.includes(this.state.search.toLowerCase())) {
                return data
            }
        }).map(data => {
            return (
                <div key={data.name}>                                                   
                    <p>{data.morpheme} {data.meaning}<br></br> {data.example}</p>
                </div>
            )
        })
 /* ADD CHILD ID */   
        return (
            <div>
            <div>
            <br></br>
            <TextField 
                placeholder='Enter morpheme'
                id='standard-basic'
                label='SEARCH'
                onChange={(e)=>this.searchSpace(e)} 
                className="SearchBar"
            />
            { items.length > 0 &&
                <Container fixed>
                    <h2>SEARCH RESULT</h2>
                    { items }
                </Container> 
            }
            </div>
            </div>
        )
    }
}

export default SearchBar;