import React from 'react';
import Morphemes from './data/Morphemes.js';
import Container from '@material-ui/core/Container';

function Suffixes() {
  const suffixes = Morphemes.suffixes.map((item) =>
    <div key={item.morpheme}>
      <p>{item.id} {item.morpheme} {item.meaning}<br></br>{item.example}</p>
    </div>
  );
  return (
    <div> 
      <Container fixed>
        <h2>SUFFIXES</h2>
        {suffixes}
     </Container>
    </div>
  );
}

export default Suffixes;
