import React from 'react';
import Morphemes from  './data/Morphemes.js';
import Container from '@material-ui/core/Container';

function Prefixes() {
  const prefixes = Morphemes.prefixes.map((item) =>
    <div key={item.morpheme}> 
      <p>{item.id} {item.morpheme} {item.meaning}<br></br> {item.example}</p>
    </div>
  );
  return (
    <div> 
      <Container fixed>
        <h2>PREFIXES</h2>
        {prefixes}
      </Container>
    </div>
  );
}

export default Prefixes;










